html,
body {
  font-family: "Roboto", sans-serif;
  padding: 0;
  margin: 0;
  background-color: #5858e3;
}

#root {
  height: 100vh;
}

* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

.particles {
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: -100;
}

.joinOuterContainer {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  text-align: center;
  height: 100%;
  -ms-flex-align: center;
      align-items: center;
  background-color: #5858e3;
  position: relative;
  z-index: 100;
}

.joinInnerContainer {
  width: 30%;
}

.joinInput {
  padding: 20px 25px;
  width: 100%;
  border: none;
  border-radius: 30px;
  -webkit-box-shadow: 1px 1px 20px 0px #00000047;
          box-shadow: 1px 1px 20px 0px #00000047;
  font-size: 15px;
}

.heading {
  color: white;
  font-size: 2.5em;
  padding-bottom: 10px;
  border-bottom: 2px solid white;
  text-transform: uppercase;
}

.button {
  color: #fff;
  text-transform: uppercase;
  text-decoration: none;
  background: #000000;
  padding: 20px;
  border-radius: 30px;
  display: inline-block;
  border-color: transparent;
  width: 100%;
  -webkit-transition-duration: 0.25s;
       -o-transition-duration: 0.25s;
          transition-duration: 0.25s;
  font-size: 15px;
  letter-spacing: 1px;
}
.button:hover {
  background: none;
  -webkit-transition-duration: 0.25 s;
       -o-transition-duration: 0.25 s;
          transition-duration: 0.25 s;
  color: black;
  border-color: white;
  border-width: 2px;
  font-size: 16px;
}

.mt-20 {
  margin-top: 20px;
}

@media (max-width: 1025px) {
  .joinOuterContainer {
    height: 100%;
  }

  .joinInnerContainer {
    width: 80%;
  }
  .particles {
    display: none;
  }
}



